import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import InputLabel from "@mui/material/InputLabel";

const dispatchedEvents = [];

const SrsMuiInputLabel = function (props, ref) {
  return (
    <InputLabel
      htmlFor={props.htmlFor}
      disableAnimation={props.disableAnimation}
      shrink={props.shrink}
      variant={props.variant}
      theme={props.muiTheme}
    >
      <slot />
    </InputLabel>
  );
};

const styles = `
  /* Custom styles for the MuiInputLabel component */
`;

const props = [
  {
    name: "htmlFor",
    defaultValue: "",
    type: "string",
  },
  {
    name: "disableAnimation",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "shrink",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "variant",
    defaultValue: "standard",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiInputLabel),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
